<template>
  <v-app>
    <v-app-bar app color="#eef5f9" elevation="2">
      <!-- <h2>以不同超商做為加盟店之綜合結果</h2> -->
      <router-link
        :to="{ path: '../main_page' }"
        :class="{ active: $route.path === '/main_page' }">
        <span class="main_t">最終結果&nbsp;:&nbsp;</span>
      </router-link>
      <router-link 
        v-for="(item, index) in link" :key="index"
        :to="{ path: `/main_page/${index}` }"
        :class="{ active: $route.path === `/main_page/${index}` }">
        &nbsp;{{ item }}&nbsp;</router-link>
      <!--  -->
      &nbsp;&nbsp;&nbsp;&nbsp;
      <span class="main_t">||</span>
      <router-link
        :to="{ path: '../other_page' }"
        :class="{ active: $route.path === '/other_page' } ">
        <span class="main_t">&nbsp;各個因素</span>
      </router-link>
      <!-- &nbsp;&nbsp;|&nbsp;人口數&nbsp;&nbsp;|&nbsp;車流量&nbsp;&nbsp;|&nbsp;競爭數量&nbsp;&nbsp;|&nbsp;租金價格&nbsp;&nbsp;|&nbsp;所得水準 -->
    </v-app-bar>
    <v-main>
      <!-- 给應用提供合適的距離 -->
      <!-- <v-container fluid> -->
      <!-- 如果使用 vue-router -->
      <router-view></router-view>
      <!-- </v-container> -->
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      tab:"",
      // title: ["7-11", "全家", "萊爾富", "OK"],
      link: {"seven":"7-11", "family":"全家", "life":"萊爾富", "ok":"OK"},
      }
    },
};
</script>

<style>
@import url(https://fonts.googleapis.com/earlyaccess/cwtexyen.css);
@import url(https://fonts.googleapis.com/earlyaccess/notosanstc.css);

body {
  font-family: "Noto Sans TC", "Helvetica", "cwTeXYen", sans-serif, Arial,
    sans-serif;
  /* line-height: 2;
  font-weight: 400; */
  color: #24201e;
  background-color: #d8d8e0;
  /* margin: 0;
  padding: 0; */
}

#app {
  font-family: "Noto Sans TC", "Helvetica", "cwTeXYen", sans-serif, Arial,
    sans-serif;
  color: #24201e;
}

.v-application a {
  color: #24201e;
}

/* nav bar */
.v-app-bar .main_t {
  font-size: 20px;
  font-weight:normal;
}
.v-app-bar a.active .main_t  {
  color: #66a3be;
  font-size: 20px;
  font-weight:600;
}

.v-app-bar a {
  color: #24201e;
  text-decoration: none;
  font-size: 16px;
  font-weight:normal;
}

.v-app-bar a.active {
  color: #66a3be;
  font-size: 20px;
  font-weight:600;
}

/* E8F5E9  519648*/

/* cf6959 */
</style>



